$gray-base: #000;
$gray-light: lighten($gray-base, 46.7%);
$gray-dark: lighten($gray-base, 20%);

$font-size-h1: 36px;
$headings-font-family: inherit;
$headings-font-weight: 500;
$headings-line-height: 1.1;
$headings-color: inherit;
$headings-small-color: $gray-light;

$font-family-sans-serif: "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-base: $font-family-sans-serif;
$font-size-base: 14px;
$line-height-base: 1.428571429;
$text-color: $gray-dark;
$body-bg: #fff;


h1, .h1 {
  font-size: $font-size-h1;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $headings-color;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: $headings-small-color;
  }
}

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
}


body {
  font-family: 'Open Sans';
}

p,
.text-align-left {
  text-align: left;
}

.text-align-right {
  text-align: right;
}

.container {
  width: 100%;
  position: absolute;
  top: 50%;
  margin-top: -180px;
}

.container .center-locator {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.container .center-locator h1{
  font-family: 'Poiret One';
  margin-top: 40px;
  margin-bottom: 40px;
}

.container .links {
  list-style-type: none;
  padding-left: 0px;
}

.container .homepage-links li {
  display: block;
}

.container .social-links li {
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
}

.container .homepage-links li a {
  display: block;
}

.container .social-links li a,
.container .bio-links li a {
  display: inline-block;
  font-size: 150%;
}

.container .links li a,
.container .links li a:link,
.container .links li a:visited,
.container .links li a:active {
  color: #888;
  text-decoration: none;
}

.container .links li a:hover {
  color: #000;
}

.container .homepage-links li::first-letter {
  font-size: 150%;
  color: #555;
}

.container .homepage-links li:hover::first-letter {
  color: #000;
}

br.clear {
  line-height: 0;
}

.bio-navigation {
  margin-top: 40px;
}

.bold {
  font-weight: 700;
}

a.text {
  color: black;
  cursor: text;
}

a:link.text,
a:visited.text,
a:hover.text,
a:active.text {
  color: black;
  text-decoration: none;
}

a.color-black,
a:link.color-black,
a:visited.color-black,
a:hover.color-black,
a:active.color-black {
  color: black;
}

ul.no-bullet,
ol.no-bullet {
  list-style-type: none;
  margin-left: -40px;
}

div.input-group-addon a {
  display:block;
  line-height: 100%;
  height: 100%;
  width: 100%;
}

music-list-detail {
  margin-top: 50px;
  display: block;
}

music-list-detail .music-list-detail-title {
  text-align: left;
}

music-list-detail .music-list-detail-audio {
  width: 100%;
}


